.body__header {
    position: relative;
    padding: .3rem 0;
    text-align: center;
}

.header__menu {
    @include flexbox();
    @include flexbox-justify-content(space-around);
    @include flexbox-align-items(center);
    width: (250rem / 16);
    margin: 0 auto;
}

.logo {
    width: (66rem / 16);
}

.header__links {
    @include flexbox();
    @include flexbox-justify-content(space-around);
    @include flexbox-align-items(center);
    @include transform-translate(0, -50%);
    position: absolute;
    width: (150rem / 16);
    height: (28.5rem / 16);
    top: 50%;
    right: 2rem;
}

.header__twitter {
    width: (36rem / 16);
}

.header__email {
    width: (30rem / 16);
}

.header__git {
    width: (29rem / 16);
}

@media only screen and (max-width: 675px) {
    .header__links {
        @include transform-translate(0, 0);
        position: relative;
        margin: 1rem auto;
        right: 0;
    }
}